import React from "react";
import styled from 'styled-components';
const Subscription = () => {
  return (
    <section>
      <SocialText>Follow me on <a href="https://twitter.com/aguidehub" rel="noopener" target="_BLANK">Twitter</a></SocialText>
      <SubscriptionContainer>
        <div dangerouslySetInnerHTML={{ __html: `<iframe src="https://aguidehub.substack.com/embed" width="100%" height="320" frameborder="0" scrolling="no"></iframe>` }} ></div>
      </SubscriptionContainer>
    </section>
  );
};

export default Subscription;

const SubscriptionContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  border: 2px solid #6166DC;
  background: #fff;
  box-shadow: 0px 0px 10px rgb(0 0 0 / 65%);
  z-index: 1;
`;

const SocialText = styled.h3`
  margin-bottom: 20px;
  text-align: center;
`;